.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.headerMenu {
  background-color: green;
  margin: 0;
  padding: 5px;
  color: #fff;
}

.footerMenu {
  background-color: rgb(61, 60, 60);
  margin: 0;
  padding: 5px;
  color: #fff;
}

.menuItem {
  display: inline-block;
  margin-right: 20px;
}

.container {
  border: 3px solid magenta;
}

.grid {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  flex-wrap: wrap;
}

.stock {
  border: 2px solid grey;
  padding: 20px;
  margin: 15px;
}

.stock img {
  height: 95px;
  width: 95px;
}

.button__main {
  padding: 15px;
  margin-top: 10px;
}

.imageTest {
  width: 80%;
  margin: 0 auto;
}

.imageTest img {
  width: 110px;
  margin: 36px;
}
